import React from "react";
import SeactionHeader from "./common/section-header";
import styled, { keyframes, ThemeProvider } from "styled-components";
import { fadeInLeftBig, fadeInRightBig } from "react-animations";
import { useHistory } from "react-router-dom";
import { devices } from "../devices";

const Container = styled.div`
  background-color: ${(props) => props.bg};
  padding: 2% 5%;
`;

const Wrapper = styled.div`
  margin: 0 auto;
  max-width: 70%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr 1fr;
  grid-gap: 15px;
  justify-items: stretch;

  @media ${devices.laptop} {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 1fr);
  }
`;

const leftAnimation = keyframes`${fadeInLeftBig}`;
const rightAnimation = keyframes`${fadeInRightBig}`;

const Card = styled.div`
  background-color: ${(props) => props.theme.cardBg};
  color: ${(props) => props.theme.cardFg};
  padding: 15px;
  border-radius: 15px;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    background-color: ${(props) => props.theme.bgHover};
  }

  & i {
    font-size: 60px;
    position: absolute;
    top: calc(50% - 30px);
    left: -10px;
    color: ${(props) => props.theme.primary};

    @media ${devices.mobileS} {
      font-size: 45px;
    }
  }
`;

const CardTitle = styled.h4`
  margin-left: 60px;

  & a {
    text-decoration: none;
    color: inherit;
  }

  @media ${devices.mobileM} {
    font-size: 20px;
  }

  @media ${devices.mobileS} {
    font-size: 17px;
    margin-left: 45px;
  }

  ${Card}:hover & {
    color: ${(props) => props.theme.fgHover};
  }
`;

const CardDescription = styled.div`
  margin-top: 10px;
  margin-left: 60px;

  @media ${devices.mobileS} {
    margin-left: 45px;
  }

  ${Card}:hover & {
    color: ${(props) => props.theme.fgHover};
  }
`;

const AnimatedLeftDiv = styled(Card)`
  animation: 1s ${leftAnimation};
`;

const AnimatedRightDiv = styled(Card)`
  animation: 1s ${rightAnimation};
`;

const Services = (props) => {
  const history = useHistory();

  return (
    <Container bg={props.theme.bg}>
      <SeactionHeader
        title={"Serviços"}
        subtitle={
          "Providenciamos uma vasta gama de serviços automóvel e ao domicílio."
        }
        headerColor={props.theme.primary}
        subtitleColor={props.theme.secundary}
      />

      <ThemeProvider theme={props.theme}>
        <Wrapper>
          <AnimatedLeftDiv
            onClick={() => {
              history.push("/carros");
            }}
          >
            <div className="box">
              <i className="fa fa-car"></i>
              <CardTitle>
                <span>Venda de carros usados</span>
              </CardTitle>
              <CardDescription>
                Vendemos todo o tipo de viaturas usadas a preços acessíveis a
                todos.
              </CardDescription>
            </div>
          </AnimatedLeftDiv>

          <AnimatedRightDiv onClick={props.handleOpenModal}>
            <div className="box">
              <i className="fas fa-house-damage"></i>
              <CardTitle>
                <span>Reparações em casa</span>
              </CardTitle>
              <CardDescription>
                Fazemos todo o tipo de reparações na sua habitação.
              </CardDescription>
            </div>
          </AnimatedRightDiv>

          <AnimatedLeftDiv onClick={props.handleOpenModal}>
            <div className="box">
              <i className="fas fa-wrench"></i>
              <CardTitle className="title">
                <span>Peças automóvel</span>
              </CardTitle>
              <CardDescription>
                Vendemos todo o tipo de peças para automóveis.
              </CardDescription>
            </div>
          </AnimatedLeftDiv>

          <AnimatedRightDiv onClick={props.handleOpenModal}>
            <div className="box">
              <i className="fas fa-comments"></i>
              <CardTitle className="title">
                <span>Aconselhamento automóvel</span>
              </CardTitle>
              <CardDescription>
                Prestamos ajuda e aconselhamento automóvel.
              </CardDescription>
            </div>
          </AnimatedRightDiv>
        </Wrapper>
      </ThemeProvider>
    </Container>
  );
};

export default Services;
